@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@500;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
body {
  font-family: "Roboto", sans-serif;
}

ul {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

textarea {
  all: unset;
}

input {
  all: unset;
}
/*  */

header {
  position: fixed;
  background-color: #fff;
}

/* containers */

.container {
  padding: 5rem;
  background-size: cover;
}

.sideContainer {
  background-color: #000;
}
.cardsContainer {
  padding: 0 0 3rem 0;
}

.servicesContainer {
}
.contactContainer {
  background-color: #ff7f26;
}

/* sideContainer */

.title {
  font-size: 3rem;
  width: fit-content;
  margin: 3rem auto 3rem;
}

.sideTitle {
  color: #fff;
}

.blockTitle {
  color: #000;
}

/* popup */

body {
  overflow-y: hidden;
}

@media (max-width: 768px) {
  .sideContainer {
    padding: 2rem;
  }
  .contactContainer {
    padding: 2rem 0;
    background-color: inherit;
  }
}
@media (max-width: 320px) {
  .servicesContainer {
    padding: 2rem;
  }
}
